:root {
  --basic: rgb(34, 34, 34);
  --grey: rgb(233, 233, 233);
  --accent: #05d584;
}
#p-categories {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content:space-evenly;
            justify-content:space-evenly;
    margin: 2em 0;
}

.p-category {
    background: var(--grey);
    border-radius: 1.5em;
    padding: .5em .8em;
    text-align: center;
    transition: background .3s ease, color .3s linear;
}

.p-category:hover {
    background: var(--basic);
    color: rgb(255, 255, 255); 
    cursor: pointer;
}

.p-category.selected {
    background: var(--basic);
    color: rgb(255, 255, 255);
}

#projects {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 1em;
    gap: 1em;
    -webkit-justify-content: stretch;
            justify-content: stretch;
}

.projprev {
    position: relative;
    height: 15em;
}

.projprev-hover {
    width: 100%;
    height: 100%;
    background: rgb(255, 255, 255);
    color:  var(--basic);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    opacity: 0;
    position: absolute;
    top: 0;
    transition: opacity .3s ease;
}

.projprev-hover:hover {
    opacity: 1;
}

.projprev img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.projprev * {
    margin: 0;
}

.projprev-title {
    margin-bottom: 1em;
}

.projprev-categories {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
#homepage {
    margin: 2em 5em 5em;
}

#header {
    text-align: center;
    padding: 2em;
    /* background: var(--basic);
    color: rgb(255, 255, 255); */
}

#h-title {
    font-size: 64px;
    margin: 0;
    cursor: pointer;
}

#header a {
    text-decoration: none;
    color: var(--basic);
}

#h-categories > * {
    margin: 0 1em;
    display: inline-block;
}

#h-categories > *:hover {
    cursor: pointer;
}

#h-categories>*:after {
    content: "";
    display: block;
    background: var(--basic);
    width: 0%;
    height: .12em;
    position:relative;
    left: 50%;
    transition: width .3s ease, left .3s ease;
}

#h-categories>*:hover:after {
    width: 100%;
    left: 0;
}

#h-categories>*.selected:after {
    width: 100%;
    left: 0;
}
#contact-area {
    text-align: center;
    background: var(--basic);
    color: rgb(255, 255, 255);
    margin: 0 auto;
    padding: 3em 0;
}

#contact-title {
    margin-top: 0;
}
.projectpage-cover {
    position: relative;
    width: 100%;
    height: 80vh;
}

.projectpage-cover img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    -webkit-filter: brightness(.9);
            filter: brightness(.9);
}

.projectpage-title {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 100px;
    color: rgb(255, 255, 255);
    font-family: Oswald;
    text-align: center;
    margin: 0;
    z-index: 1;
    -webkit-filter: drop-shadow(0 0 .5em rgba(0, 0, 0, 0.5));
            filter: drop-shadow(0 0 .5em rgba(0, 0, 0, 0.5));
}

.projectpage-info {
    display: grid;
    grid-template-columns: -webkit-max-content -webkit-max-content auto;
    grid-template-columns: max-content max-content auto;
    grid-gap: 2em;
    gap: 2em;
    margin: 5em 8em;
    list-style: none;
}

.info-area, .info-list{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.info-name {
    color: var(--accent);
    margin-bottom: .5em;
}

.projectpage a {
    text-decoration: none;
    color: var(--accent);
    transition: color .3s ease;
}

.projectpage a:hover {
    color: #009159;
}
:root {
    --colGallery: 3;
}

.gallery {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-columns: repeat(var(--colGallery), 1fr);
    grid-gap: 1em;
    gap: 1em;
    margin: 5em 8em;
    justify-items: center;
    grid-auto-rows: 13em;
    /* background: var(--grey); */
}

.gallery > *:not(.lighthouse) {
    width: 100%;
    height: 100%;
    /* width: 20em;
    height: 13em; */
    cursor: pointer;
    position: relative;
    top: 0;
    transition: width .3s ease, heigth .3s ease, top .3s ease;
    object-fit: cover;
}

.gallery>*:not(.lighthouse):hover {
    /* width: calc(1.05 * 20em);
    height: calc(1.05 * 13em);
    top: calc(-0.025 * 20em);     */
    width: 105%;
    height: 105%;
    top: -2.5%;

}

.play-icon {
    height: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-filter: brightness(2) drop-shadow(0 0 .5em rgba(0, 0, 0, 0.5));
            filter: brightness(2) drop-shadow(0 0 .5em rgba(0, 0, 0, 0.5));
}

.videothumbnail-img{
    width: inherit;
    height: inherit;
    object-fit: cover;
}
.lighthouse {
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(34, 34, 34, 0.98);
    width: 100vw;
    height: 100vh;
    padding: 2em;
    display: grid;
    grid-template-rows: calc(100% - 10em) 5em;
    grid-template-columns: 5em calc(100% - 16em) 5em;
    grid-template-areas: 
    "l-arrow current r-arrow"
    "thumbnails thumbnails thumbnails";
    grid-gap: 1em;
    gap: 1em;
    justify-items: center;
    -webkit-align-items: center;
            align-items: center;
}

.l-items {
    height: 100%;
    width: 100%;
    grid-area: thumbnails;
    /* display: grid;
    grid-template-columns: repeat(9, calc(100%/9 - .4em));
    grid-template-rows: 100%;
    gap: .5em; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    position: relative;
    overflow-x: auto;
    overflow-y: hidden;
}

.l-items > * {
    height: 4.8em;
    width: 7em;
    cursor: pointer;
    object-fit: cover;
    margin-right: .5em;
    position: relative;
}

.currentThumbnail {
    border: solid rgb(255, 255, 255) .1em;
    /* filter: brightness(100); */
}

.currentPicture {
    grid-area: current;
    width: 100%;
    height: 100%;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.currentPicture > * {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

/* .currentPicture > .video {
    width: 100%;
    height: 100%;
} */

.left-arrow {
    grid-area: l-arrow;
}

.right-arrow {
    grid-area: r-arrow;
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
}

.left-arrow, .right-arrow {
    width: 2em;
    -webkit-filter: brightness(.3);
            filter: brightness(.3);
    cursor: pointer;
    transition: -webkit-filter .3s ease;
    transition: filter .3s ease;
    transition: filter .3s ease, -webkit-filter .3s ease;
}

.left-arrow:hover, .right-arrow:hover {
    -webkit-filter: brightness(1);
            filter: brightness(1);
}

.close {
    width: 1.2em;
    height: auto;
    position: absolute;
    top: 3em;
    right: 8em;
    cursor: pointer;
    -webkit-filter: brightness(.3);
            filter: brightness(.3);
    transition: -webkit-filter .3s ease;
    transition: filter .3s ease;
    transition: filter .3s ease, -webkit-filter .3s ease;
}

.close:hover {
    -webkit-filter: brightness(1);
            filter: brightness(1);
}

/* width */
::-webkit-scrollbar {
    width: 1em;
}
  
/* Track */
::-webkit-scrollbar-track {
    background:rgb(94, 94, 94);
}
   
/* Handle */
::-webkit-scrollbar-thumb {
    background: rgb(182, 182, 182); 
    border-radius: 1em;
}
  
/* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(255, 255, 255); 
}
